import React from 'react';

export default () => {
  return (
    <div>
      <h1 className="display-4">
        <b className="text-danger">404</b> Page Not Found
      </h1>
      <p className="lead">
        <b>Sorry</b> That Page does not exit !
      </p>
    </div>
  );
};



